
:root {
  --color-p : #5E60CE;
  --color-lp : #8284FA;
  --color-b1 : #0D0D0D;
  --color-b2 : #262626;
  --color-body1 : #F2F2F2;
  --color-bd : #1E6F9F;
  --radius-btn : 0.8rem;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  background: #1A1A1A;
  color: var(--color-body1);
}

.App {
  text-align: center;
}
input {box-shadow: none;}

.Header {width: 100%; font-size: 4rem; padding: 7.2rem 0 8rem 0; background: var(--color-b1); color: #fff; font-weight: 800;}
.Header i {color: var(--color-p); font-size: 3rem; padding-right: 1rem; transform: rotate(-45deg) translate(0.25rem, -0.5rem); font-size: 2.8rem;}
.Header span:first-of-type { color: #4EA8DE;}
.Header span:last-of-type { color: #1E6F9F;}
.Header nav {display: none;}

main {max-width: 73.6rem; margin:0 auto 0; padding-bottom: 4rem; min-height: calc(100vh - 19.2rem);}
.inputArea {display: flex; gap:0.8rem; border: 1px solid; border: 1px solid var(--color-b1); transform: translateY(-50%);}
.inputArea input {flex: 1; border:var(--color-b1); background: var(--color-b2); padding: 1.6rem; border-radius: var(--radius-btn);}
.inputArea button {font-size: 1.4rem; width:8.7rem ; height: 5.2rem ; background: var(--color-bd) ;  border-radius: var(--radius-btn); }
.inputArea button::after {content: '\2b'; font:var(--fa-style-family-classic);margin-left: 0.5rem;}

main .count {width: 100%; text-align: right; padding-bottom: 2.4rem; color: var(--color-lp); font-weight: 600; padding-top: calc(6.4 - 5.2/2);}
main .count span {background: #ccc; color: #fff; border-radius: 100rem; display: inline-block; font-size: 1.4rem; padding: 0.5rem 1.6rem; margin-left: 0.5rem;}

.listCon li:not(.noneList) {display: flex; justify-content: space-between; align-items: center; border: 1px solid #ccc; padding:1.6rem; border-radius: var(--radius-btn); margin-bottom: 1.2rem; background: var(--color-b2); color: #F2F2F2; font-weight: 400;}
.listCon li.noneList {border-top: 1px solid #333; padding: 6.4rem 0;}
.listCon li i {color: #808080;}
.listCon li i:hover {color: #F2F2F2;}

.Modal {position: fixed; top: 0; left:0; width:100%; height: 100vh; background: rgba(0, 0, 0, 0.8); display: flex; justify-content: center; align-items: flex-start;}
.Modal ._inner { background: var(--color-body1); color: var(--color-b1); position: relative; width: calc(100% - 4.6rem); max-width: 60rem; padding: 2.4rem; margin-top: 10rem;}
.Modal ._inner > i {position: absolute; right: -2.4rem; top: -2.4rem; color: #fff; cursor: pointer;}